button{
    cursor: pointer;
    color: #e20074;
    background-color: white;
    border: none;
    padding: 0.625rem 1.5rem;
    font-family: Helvetica;
    font-weight: bold;
    font-size: 0.875rem;
}
button:hover {
    background-color: #e20074;
    color: #fff;
}
.img-button{
    cursor: pointer;
    width: 30%;
}